:root {
   --bodyColor: rgb(20, 20, 20);
   --contrastingBodyColor: rgb(30, 30, 30);
   --textColor: white;
   --contrastingTextColor: black;
   --yellowColor: rgb(196, 199, 0);
   --greenColor: #36ff90;
   --greenTextColor:#36a568 ;
   --darkGreenColor: #36a568;
   --blueColor: #45daff;
   --lineColor: white;
   --hamburgerColor: brightness(100%);
}
$borderRadius: 15px;
html {
   overflow-y: hidden;
   overflow-x: visible;
}
body {
   background-color: var(--bodyColor);
   margin: 0;
}
::-webkit-scrollbar {
   width: 15px;
}
::-webkit-scrollbar-track {
   background-color: var(--bodyColor);
}
::-webkit-scrollbar-thumb {
   background: var(--contrastingBodyColor);
   border-radius: 2px;
}


@mixin exitIcon {
   user-select: none;
   position: absolute;
   right: 0;
   top: 0;
   cursor: pointer;
   content: url("../resources/exit.svg");
   height: 50px;
   transition: 0.2s;
   filter: var(--hamburgerColor);
   &:hover {
      opacity: 0.8;
   }
}

.mainUi {
   height: 100vh;
   width: 100vw;
   display: flex;
   font-family: 'Roboto', sans-serif;

   .YourInvites{
      display: block;
      position: fixed;
      padding: 30px 70px 70px 70px;
      text-align: center;
      border-radius: $borderRadius;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: 1;
      background-color: var(--bodyColor);
      color: var(--textColor);
      a{
         @include exitIcon;
      }
      .title{
         font-size: 40px;
         margin-bottom: 20px;
      }
      .noInvites{
         font-size: 20px;
         text-decoration:underline;
      }
      .individualInvite{
         padding:3px;
         margin-bottom: 2px;
         font-size: 20px;
         background-color:var(--contrastingBodyColor);
         @mixin acceptDeclineButton {
            border: none;
            font-size: inherit;
            font-family: inherit;
            transition:0.13s;
            cursor: pointer;
         }
         .accept{
            @include acceptDeclineButton;
            background-color: var(--greenColor);
            margin-left:5px;
            &:hover{
               background-color: var(--darkGreenColor);
               color: var(--textColor);
            }
         }
         .decline{
            @include acceptDeclineButton;
            background-color: rgb(187, 42, 42);
            color: var(--textColor);
            &:hover{
               background-color: rgb(129, 31, 31);
            }
         }
      }
   }

   .CreateChat {
      display: block;
      position: fixed;
      padding: 30px 70px 70px 70px;
      text-align: center;
      border-radius: $borderRadius;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: 1;
      background-color: var(--contrastingBodyColor);
      color: var(--textColor);
      a {
         @include exitIcon;
      }
      .title {
         font-size: 40px;
         margin-bottom: 30px;
      }
      .Create {
         font-size: 25px;
         cursor: pointer;
         background-color: var(--greenColor);
         color: var(--contrastingTextColor);
         transition: 0.2s;
         border: none;
         outline: none;
         &:hover {
            background-color: var(--darkGreenColor);
            color: var(--textColor);
         }
      }
      input {
         color: var(--greenColor);
         border: none;
         outline: none;
         font-size: 25px;
         background-color: var(--bodyColor);
      }
   }
   .internetWarningPopUp {
      display: block;
      position: fixed;
      background-color: var(--contrastingBodyColor);
      border: solid var(--lineColor);
      border-width: 1px;
      border-radius: 10px;
      text-align: center;
      color: var(--textColor);
      font-size: 30px;
      z-index: 2;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      padding: 20px;
   }
   .grayBackground {
      z-index: 1;
      position: fixed;
      height: 100vh;
      width: 100vw;
      background-color: black;
      opacity: 0.4;
      transition: .2s;
      &:hover{
         opacity: 0.3;
      }
   }
   .ChatInfo {
      z-index: 1;
      position: fixed;
      top: 50%;
      left: 50%;
      background-color: var(--contrastingBodyColor);
      text-align: center;
      color: var(--textColor);
      transform: translate(-50%, -50%);
      padding: 30px 70px 70px 70px;
      border-radius: $borderRadius;
      a {
         @include exitIcon;
      }
      .title {
         font-size: 40px;
         margin-bottom: 20px;
      }
      .subTitle {
         font-size: 25px;
         margin-bottom: 5px;
      }
      .adminInfo{
         font-size:20px;
         margin-left:5px;
         color:var(--greenColor)
      }
      .info{
         font-size:20px;
         color:var(--greenColor)
      }
   }
   .Settings {
      opacity: 1;
      position: fixed;
      z-index: 1;
      border-radius: $borderRadius;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      padding: 30px 70px 70px 70px;
      background-color: var(--contrastingBodyColor);
      text-align: center;
      a{
         @include exitIcon;
      }
      .title {
         color: var(--textColor);
         font-size: 40px;
         margin-bottom: 20px;
      }
      div {
         color: var(--textColor);
         font-size: 20px;
      }
      select {
         outline: none;
         font-family: inherit;
         margin-top: 5px;
         font-size: 25px;
         border-radius: 5px;
         cursor: pointer;
      }
   }
   .HamburgerMenu {
       
      background-color: var(--bodyColor);
      display: block;
      position: absolute;
      top: 50px;
      right: 0;
      text-align: center;
      padding: 50px;
      box-shadow: -15px 16px 20px 0px #00000024;
      border-radius:0 0 0 $borderRadius;
      .youAreLoggedInAs {
         margin: 20px 0 20px 0;
         color: var(--yellowColor);
         font-size: 30px;
      }
      button {
         font-family: inherit;
         text-decoration: none;
         color: var(--contrastingTextColor);
         margin: 10px 0 10px 0;
         text-align: center;
         outline: none;
         -webkit-tap-highlight-color: transparent;
         font-size: 30px;
         cursor: pointer;
         transition: all 0.2s cubic-bezier(0.23, 1.65, 0.38, 1.65);
         border: none;
         border-radius: 5px;
         padding: 3px 5px 3px 5px;
         background-color: var(--greenColor);
          
         width: 200px;
         &:hover {
            background-color: var(--darkGreenColor);
            margin: 3px 0 3px 0;
            padding: 10px;
            color: var(--textColor);
         }
      }
      .links {
         margin: 20px 0 0 0;
         pre {
            display: inline;
            color: var(--textColor);
            font-size: 20px;
         }
         a {
            color: var(--textColor);
            font-size: 20px;
         }
      }
   }
   .InviteMenu{
      z-index: 1;
      position: fixed;
      top: 50%;
      left: 50%;
      background-color: var(--contrastingBodyColor);
      text-align: center;
      color: var(--textColor);
      transform: translate(-50%, -50%);
      padding: 30px 70px 70px 70px;
      border-radius: $borderRadius;
       
      a {
         @include exitIcon;
      }
      .title {
         font-size: 40px;
         margin-bottom: 20px;
      }
      .subTitle{
         font-size:20px;
         margin-bottom:5px;
      }
      input{
         color: var(--greenColor);
         border: none;
         outline: none;
         font-size: 25px;
         background-color: var(--bodyColor);
      }
      button{
         outline:none;
         border: none;
         font-size: 25px;
         background-color: var(--greenColor);
         cursor: pointer;
         transition: .3s;
         &:hover{
            color: var(--textColor);
            background-color: var(--darkGreenColor);
         }
      }
      .message{
         font-size: 25px;
         background-color: rgb(187, 42, 42);
         color: var(--textColor);
         border: none;
      }
   }
   .ChatMenu {
      display: block;
      height: 100%;
      width: 400px;
      background-color: var(--contrastingBodyColor);
      color: var(--textColor);
      overflow: auto;
       
      font-size: 30px;
      text-align: center;
      .topBar {
         display: none;
      }
      .individualChat {
         padding: 2px;
         cursor: pointer;
         user-select: none;
         &:hover {
            background-color: var(--bodyColor);
         }
      }
      .createChatButton {
         outline: none;
         border: solid 1px var(--lineColor);
         background-color: transparent;
         color: var(--greenColor);
         padding: 0 25px 0 25px;
         position: absolute;
         border-radius: 99px;
         font-size: 80px;
         bottom: 20px;
         left: 20px;
         cursor: pointer;
         &:hover {
            background-color: var(--bodyColor);
         }
      }
   }

   .rightContainer {
      height: 100%;
      width: calc(100% - 400px);
      display: block;
      overflow-y: visible;
      overflow-x: hidden;
      .TopBar {
         height: 50px;
         width: 100%;
         color: var(--textColor);
         background-color: var(--contrastingBodyColor);
          
         text-align: center;
         div {
            user-select: none;
            display: inline;
            font-size: 40px;
         }
         .hamburgerButton {
            user-select: none;
            position: absolute;
            right: 0;
            cursor: pointer;
            content: url("../resources/hamburger.svg");
            filter: var(--hamburgerColor);
            height: inherit;
            transition: 0.2s;
            &:hover {
               opacity: 0.8;
            }
         }
      }

      .TextsUi {
         height: calc(100% - 50px);
         width: 100%;
         .messagesView {
            overflow-x: hidden;
            overflow-y: auto;
            position: static;
            margin: 0;
            height: calc(100% - 90px);
            width: 100%;

            @mixin message {
               position: static;
               text-align: left;
               word-wrap: break-word;
               padding: 15px 40px 15px 40px;
               border: solid;
               border-width: 0 0 1px 0;
               border-radius: 8px;
               border-color: var(--lineColor) !important;
               margin: 0;
               font-size: 30px;
               
               .info {
                  display: block;
                  line-height: 30px;
                  .sender {
                     display: inline-block;
                     font-size: 20px;
                  }
                  .time {
                     line-height: 25px;
                     display: inline-block;
                     color: inherit;
                     font-size: 15px;
                     vertical-align: bottom;
                     margin-left: 0.5em;
                  }
               }

            }
            .yourMessage{
               @include message;
               color: var(--blueColor);
            }
            .othersMessage{
               @include message;
               color: var(--greenTextColor)
            }
         }
         .messageInputs {
            background-color: var(--bodyColor);
            bottom: 0;
            right: 0;
            left: 0;
            display: flex;
            position: static;
            font-size: 30px;
            height: 90px;
            margin: 0;
            z-index: 0;
            textarea {
               font-family: inherit;
               outline: none;
               color: var(--textColor);
               background-color: var(--bodyColor);
               display: inline-block;
               margin: 0;
               height: 100%;
               width: 100%;
               position: static;
               font-size: 30px;
               resize: none;
               padding: 0px 2px 0px 15px;
               border: none;
            }
            button {
               display: none;
               color: var(--textColor);
               background-color: var(--bodyColor);
               margin: 0;
               height: 100%;
               position: static;
               font-size: 30px;
               line-height: 1em;
                
               border: none;
               cursor: pointer;
            }
         }
      }
   }
}
@media only screen and (max-device-width: 1300px) and (orientation: portrait) {
   .mainUi {
      .chatMenu {
         width: 300px;
      }
      .rightContainer {
         width: calc(100% - 300px);
         .TextsUi .messageInputs {
            textarea {
               width: 80%;
            }
            button {
               display: block;
               width: 20%;
            }
         }
      }
   }
}
@media only screen and (max-device-width: 1400px) and (orientation: landscape) {
   .mainUi {
      .chatMenu {
         width: 400px;
      }
      .rightContainer {
         width: calc(100% - 400px);
         .TextsUi {
            .messageInputs {
               textarea {
                  width: 80%;
               }
               button {
                  display: block;
                  width: 20%;
               }
            }
         }
      }
   }
}
@media only screen and (max-device-width: 760px) and (orientation: portrait) {
   html {
      overflow-y: visible;
      overflow-x: visible;
   }
   .mainUi {
      .Settings {
         a {
            height: 40px;
         }
      }
      .HamburgerMenu {
         position: static;
         padding: 50px 0 0 0;
         top: 40px;
         box-shadow: none;
         .exitButton {
            @include exitIcon;
            height: 40px;
         }
      }
      .ChatMenu {
         width: 100%;
         .topBar {
            display: block;
            font-size: 30px;
            height: 40px;
            .exitButton {
               position: absolute;
               top: 0;
               left: 0;
               height: 40px;
               content: url("../resources/exit.svg");
            }
         }
         .chats {
            overflow: auto;
            height: calc(100% - 40px);
            .individualChat {
               border: solid var(--lineColor);
               border-width: 1px 0 0 0;
               border-radius: 3px;
            }
         }
      }
      .InviteMenu{
         padding: 20px 40px 40px 40px;
      }
      .CreateChat{
         padding: 20px 40px 40px 40px;
      }
      .rightContainer {
         width: 100%;
         .TopBar {
            position: fixed;
            top: 0;
            height: 40px;
            div {
               font-size: 30px;
            }
            .chatButton {
               content: url("../resources/chatButton.svg");
               position: absolute;
               left: 0;
            }
            a {
               -webkit-tap-highlight-color: transparent;
               height: inherit;
            }
         }
         .TextsUi {
            height: 100%;
            .messagesView {
               height: calc(100% - 110px);
               padding-top: 40px;

               .yourMessage{
                  padding: 5px 20px 5px 20px;
                  font-size: 20px;
                  .info {
                     line-height: 20px;
                     .sender {
                        font-size: 13px;
                     }
                     .time {
                        line-height: 16px;
                        font-size: 10px;
                        margin-left: 0.5em;
                     }
                  }
               }
               .othersMessage{
                  padding: 5px 20px 5px 20px;
                  font-size: 20px;
                  .info {
                     line-height: 20px;
                     .sender {
                        font-size: 13px;
                     }
                     .time {
                        line-height: 16px;
                        font-size: 10px;
                        margin-left: 0.5em;
                     }
                  }
               }

            }
            .messageInputs {
               height: 70px;
               textarea {
                  width: 80%;
                  font-size: 22px;
               }
               button {
                  display: block;
                  -webkit-tap-highlight-color: transparent;
                  outline: none;
                  width: 20%;
                  font-size: 22px;
               }
            }
         }
      }
   }
}
